@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700);
.cell {
	width: 15px;
	height: 15px;
	outline: 1px solid rgb(28, 43, 43);
	/* outline-width: .53mm; */
	display: inline-block;
	background-color: white;
}

.cell-finish {
	background-color: tomato;
}

.cell-start {
	background-color: blue;
}

.cell-visited {
	/* background-color: yellow; */
	-webkit-animation-name: visitedAnimation;
	        animation-name: visitedAnimation;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-delay: 0;
	        animation-delay: 0;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: running;
	        animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
	from {
	  transform: scale(0.3);
	  background-color: rgba(0, 0, 66, 0.75);
	  border-radius: 100%;
	}
  
	50% {
	  background-color: rgba(235, 202, 111, 0.75);
	}
  
	75% {
	  transform: scale(1.2);
	  background-color: rgba(230, 219, 65, 0.75);
	}
  
	to {
	  transform: scale(1);
	  background-color: rgb(207, 194, 8);
	}
  }

@keyframes visitedAnimation {
	from {
	  transform: scale(0.3);
	  background-color: rgba(0, 0, 66, 0.75);
	  border-radius: 100%;
	}
  
	50% {
	  background-color: rgba(235, 202, 111, 0.75);
	}
  
	75% {
	  transform: scale(1.2);
	  background-color: rgba(230, 219, 65, 0.75);
	}
  
	to {
	  transform: scale(1);
	  background-color: rgb(207, 194, 8);
	}
  }

.cell-backtrack {
	/* background-color: green; */
	-webkit-animation-name: shortestPath;
	        animation-name: shortestPath;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-delay: 0;
	        animation-delay: 0;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: running;
	        animation-play-state: running;
}

@-webkit-keyframes shortestPath {

	
	from {
	  transform: scale(0.6);
	  background-color:  rgb(145, 180, 136);
	}
  
	50% {
	  transform: scale(1.2);
	  background-color:  rgb(101, 180, 81);
	}
  
	to {
	  transform: scale(1);
	  background-color: rgb(43, 180, 8);
	}
  }

@keyframes shortestPath {

	
	from {
	  transform: scale(0.6);
	  background-color:  rgb(145, 180, 136);
	}
  
	50% {
	  transform: scale(1.2);
	  background-color:  rgb(101, 180, 81);
	}
  
	to {
	  transform: scale(1);
	  background-color: rgb(43, 180, 8);
	}
  }

